export default class Parallax {
  constructor(el, parent) {
    /** @type {Element} */
    this.el = el // the IO target element
    this.listener = null

    this.speedy = 0
    this.speedx = 0
    this.firstRender = true

    // work on element without IO
    this.handle = this.el.nextElementSibling
    const noscript = this.handle.nextElementSibling

    // cleanup DOM
    if (noscript.tagName != 'NOSCRIPT') {
      console.warn('Could not remove noscript element.')
    } else parent.removeChild(noscript)
  }
  unrender() {
    if (this.listener) {
      window.removeEventListener('scroll', this.listener)
    }
  }
  compute() {
    // use el to prevent against resizes.
    const box = this.el.getBoundingClientRect()
    const { top, height, width } = box
    const d = top - window.innerHeight

    const maxScrollY = -(-window.innerHeight - height)
    // not accounting for horizontal scrolling yet
    const maxY = maxScrollY * Math.abs(this.speedy)
    this.handle.style['min-height'] = height + maxY + 'px'

    const maxX = maxScrollY * Math.abs(this.speedx)
    this.handle.style['min-width'] = width + maxX + 'px'

    let Y = d * this.speedy
    let X = d * this.speedx
    if (this.speedx < 0) X -= maxX
    if (this.speedy < 0) Y -= maxY

    const t = `translate3d(${floatToPx(X)}, ${floatToPx(Y)}, 0)`
    this.handle.style.transform = t
    this.handle.style.webkitTransform = t
  }  /**
   * @param {function(Error, *)} callback
   * @param {Element} el
   * @suppress {checkTypes}
   */
  static 'load'(callback, el, {
    'background-image': backgroundImage,
    splendid: { mount },
  }) {
    const img = document.createElement('img')
    const src = `${mount}${backgroundImage}`
    img.src = src
    img.onload = () => {
      callback(null, { src, class: el.className, style: el.getAttribute('style') })
    }
    img.onerror = () => {
      callback(new Error('Image could not be loaded.'))
    }
  }
  /**
   * @suppress {checkTypes}
   */
  static get 'plain'() {
    return true
  }
  render({ speedy = 0.5, speedx = 0, src }) {
    if (this.firstRender) {
      this.handle.className += ' RunFadeIn'
      this.firstRender = false
    }
    this.speedy = parseFloat(speedy)
    this.speedx = parseFloat(speedx)

    this.listener = () => {
      this.compute()
    }
    window.addEventListener('scroll', this.listener)
    this.handle.style['background-image'] = `url(${src})`
    this.compute()
  }
}

// export const plain = true

const floatToPx = (f) => {
  return `${f.toFixed()}px`
}

